<template>
    <section class="banner-area" id="home">
        <div class="container">
            <div class="row fullscreen d-flex align-items-center justify-content-center">
                <div class="banner-content col-lg-6 col-md-9">
                    <h1>
                        Machine Learning		
                    </h1>
                    <p class="text-white text-uppercase">
                        Tebak jurusan kuliah berdasarkan judul Skripsi
                    </p>
                    
                    <input v-model="title" type="text" class="form-control col-sm-10" placeholder="Ketik judul skripsi..">
                    <button @click="submit" class="mt-2 primary-btn header-btn">Tebak Sekarang!</button>

                    <p v-if="result" class="text-result mt-4 text-white">
                        {{ result }}
                    </p>
                </div>
                <div class="banner-img col-lg-6 col-md-3">
                    <img class="img-fluid" src="img/banner-img.png" alt="">
                </div>												
            </div>
        </div>
    </section>
</template>

<script>
import { stemmerProcess } from '../logic/stemmer'
import { training } from '../logic/training'
import { testing } from '../logic/test'

export default {
    name: 'appHeader',
    data () {
        return {
            title: '',
            data: [],
            words: [],
            training: [],
            result: null
        }
    },
    created () {
        let stemmerResult = stemmerProcess()
        this.data = stemmerResult.data
        this.words = stemmerResult.words

        this.training = training(this.data, this.words)
    },
    methods: {
        submit () {
            try {
                if (this.title === '' || this.title === null) return
                this.result = 'Jurusan kamu ' + testing(this.training, this.title)
            } catch (err) {
                this.result = 'Maaf, belum dapat mengidentifikasi :('
            }
        }
    }
}
</script>
