var departements = ['teknik informatika', 'manajemen', 'biologi']
var departementWords = [
    {
        class: 'teknik informatika',
        words: [],
        pData: 0, // jumlah data berdasarkan label / jumlah semua data
        computed: []
    },
    {
        class: 'manajemen',
        words: [],
        pData: 0,
        computed: []
    },
    {
        class: 'biologi',
        words: [],
        pData: 0,
        computed: []
    },
]

function filterDataByDepartement(departement, data) {
    return data.filter(item => {
        return (item.class === departement)
    })
}

function prepareWordsEachDepartement(words) {
    for (let i = 0; i < departements.length; i++) {
        let index = departementWords.findIndex(item => {
            return item.class === departements[i]
        })

        for (let j = 0; j < words.length; j++) {
            departementWords[index].words.push({ word: words[j], count: 0 })
        }
    }
}

function trainingByDepartement(departement, data) { // departement = data class
    // prepare data
    let departementData = filterDataByDepartement(departement, data)
    let index = departementWords.findIndex(item => {
        return item.class === departement
    })

    // counting each words in each class
    for (let i = 0; i < departementData.length; i++) {
        let splits = departementData[i].text.split(' ')
        for (let j = 0; j < splits.length; j++) {
            let found = departementWords[index].words.findIndex(item => {
                return item.word === splits[j]
            })

            if (found > -1) departementWords[index].words[found].count += 1
        }
    }

    // prepare computing
    departementWords[index].pData = departementData.length / data.length
    let wordsFound = departementWords[index].words.filter(item => {
        return item.count !== 0
    })

    // computing each words to number
    for (let i = 0; i < departementWords[index].words.length; i++) {
        departementWords[index].computed
            .push({
                word: departementWords[index].words[i].word,
                value: (departementWords[index].words[i].count + 1)
                        / (wordsFound.length + departementWords[index].words.length)
            })
    }
}

function training(data, words) {
    prepareWordsEachDepartement(words)
    
    for (let i = 0; i < departements.length; i++) {
        trainingByDepartement(departements[i], data)
    }
    return departementWords
}

export { training, departements }