import sastrawi from 'sastrawijs'
import { departements } from './training'

var stemmer = new sastrawi.Stemmer()
var tokenizer = new sastrawi.Tokenizer()

var computedTest = [
    {
        class: 'teknik informatika',
        pData: 0,
        computed: [],
        result: 1
    },
    {
        class: 'manajemen',
        pData: 0,
        computed: [],
        result: 1
    },
    {
        class: 'biologi',
        pData: 0,
        computed: [],
        result: 1
    },
]

var result = []

function departementComputed(departement, title, training) {
    let stemmed = []
    let word = null
    let tokenizerResult = tokenizer.tokenize(title)

    for (word of tokenizerResult) {
        stemmed.push(stemmer.stem(word))
    }

    let data = training.filter(item => {
        return item.class === departement
    })

    let index = computedTest.findIndex(item => {
        return item.class === departement
    })
    computedTest[index].computed.push(data[0].pData)

    for (let i = 0; i < stemmed.length; i++) {
        let match = data[0].computed.filter(item => {
            return item.word === stemmed[i]
        })

        if (match.length > 0) {
            computedTest[index].computed.push(match[0].value)
        } else {
            let trainingIndex = training.filter(item => {
                return item.class === departement
            })
            let wordsFound = trainingIndex[0].words.filter(item => {
                return item.count !== 0
            })
            computedTest[index].computed.push((0 + 1) / (wordsFound.length + trainingIndex[0].words.length))
        }
    }

    for (let i = 0; i < computedTest[index].computed.length; i++) {
        computedTest[index].result *= computedTest[index].computed[i]
    }

    result.push(computedTest[index].result)
}

function reset() {
    computedTest = [
        {
            class: 'teknik informatika',
            pData: 0,
            computed: [],
            result: 1
        },
        {
            class: 'manajemen',
            pData: 0,
            computed: [],
            result: 1
        },
        {
            class: 'biologi',
            pData: 0,
            computed: [],
            result: 1
        },
    ]
    
    result = []
}

export function testing(training, title) {
    for (let i = 0; i < departements.length; i++) {
        departementComputed(departements[i], title, training)
    }

    let final = Math.max(...result)
    let index = computedTest.filter(item => {
        return item.result === final
    })

    let finalResult = index[0].class
    reset()

    return finalResult
}