import data from './data'
import sastrawi from 'sastrawijs'

var words = []
var stemmer = new sastrawi.Stemmer()
var tokenizer = new sastrawi.Tokenizer()

function dataStemmer() {
    for (let i = 0; i < data.length; i++) {
        let stemmed = []
        let word = null
        let tokenizerResult = tokenizer.tokenize(data[i].text)
        for (word of tokenizerResult) {
            let temp = stemmer.stem(word)
            stemmed.push(temp)
            words.push(temp)
        }

        data[i].text = stemmed.join(' ')
    }

    // make all words value is unique
    words = words.filter((v, i, a) => a.indexOf(v) === i)
}

export function stemmerProcess() {
    dataStemmer()
    return { data, words }
}